<template>
  <div>
    <div class="loginStyle">
      <span @click="gohome">动画效果</span>
      <input type="text">
    </div>
    <div class="code" @click="refreshCode">
      <s-identify :identifyCode="identifyCode"></s-identify>
    </div>
    <template>
      <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
        <!-- slides -->
        <swiper-slide style="background-color:#0094ff;">I'm Slide 1</swiper-slide>
        <swiper-slide style="background-color:red;">I'm Slide 2</swiper-slide>
        <swiper-slide style="background-color:blanchedalmond;">I'm Slide 3</swiper-slide>
        <swiper-slide style="background-color:#cbff97;">I'm Slide 4</swiper-slide>
        <swiper-slide style="background-color:#3aff50;">I'm Slide 5</swiper-slide>
        <swiper-slide style="background-color:#ff34fc;">I'm Slide 6</swiper-slide>
        <swiper-slide style="background-color:#3ffaff;">I'm Slide 7</swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
        <!--<div class="swiper-button-prev" slot="button-prev"></div>-->
        <!--<div class="swiper-button-next" slot="button-next"></div>-->
        <!--<div class="swiper-scrollbar"   slot="scrollbar"></div>-->
      </swiper>
    </template>

  </div>


</template>

<script>
  import SIdentify from '../identify.vue'
  import 'swiper/dist/css/swiper.css'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default{
    /*给组件取一个别名*/
    name: 'login',
    data(){
      return{
        identifyCodes: "1234567890",
        identifyCode: "",

        swiperOption: {
          // some swiper options/callbacks
          // 所有的参数同 swiper 官方 api 参数
          // ...
          autoplay: true,
          loop: true,
          centeredSlides : true,
          slidesPerView : 2,
          spaceBetween : 20,
        }
      }
    },
    mounted() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);

      // current swiper instance
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      console.log('this is current swiper instance object', this.swiper)
      this.swiper.slideTo(3, 1000, false)
    },
    methods:{
      gohome(){
        this.$router.push('home');
      },
      randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      refreshCode() {
        this.identifyCode = "";
        this.makeCode(this.identifyCodes, 4);
      },
      makeCode(o, l) {
        for (let i = 0; i < l; i++) {
          this.identifyCode += this.identifyCodes[
            this.randomNum(0, this.identifyCodes.length)
            ];
        }
        console.log(this.identifyCode);
      },
      callback(){
        console.log('点击了图片');
      },
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    components:{
      SIdentify,
      swiper,
      swiperSlide
    }
  }
</script>

<style scioed>
  .loginStyle{
    height: 500px;
    width: 100%;
    background-color: aqua;
  }
  .code {
    margin: 400px auto;
    width: 114px;
    height: 40px;
    border: 1px solid red;
  }
  .swiper-slide{
    height: 500px;
  }
</style>



